.container {
  padding-inline: 2rem;
}

.contactForm {
  display: flex;
  align-items: center;
  justify-content: center;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  max-width: 40rem;
}

.buttonContainer > button {
  float: right;
}

.error {
  height: 1.5rem;
  color: var(--red);
}
